import React, { Component } from 'react'
class List extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
     <div>
        <ul>
        {this.props.renderTodos(false)}
        </ul>
    <h2>已经完成</h2>
    <ul style={{color:'red'}}>
        {this.props.renderTodos(true)}
    </ul>
    </div>
        )
    }
}
export default List;